import MainApi   from "@/app/http/api/models/main-api";
import helpers   from "@/app/utils/helpers";
import MainError from "@/app/utils/swal/errors/main-error";
import http      from 'axios'

class DownloadApi {

  static request(method, url, data = null, headers = null) {
    let config = (new MainApi()).config(method, url, data, headers);
    config.responseType = 'arraybuffer';
    return new Promise((resolve, reject) => {
      helpers.loading()
      http(config)
        .then((response) => resolve(response && response.data && response.data.data ? response.data.data : response))
        .catch((error) => {
          let errorMsg = error && error.response && error.response.status === 404 ? "Le fichier est introuvable" : "Une erreur est survenue, merci de réessayer plus tard."
          MainError.fire({ text: errorMsg });
          return reject(errorMsg)
        })
        .finally(() => helpers.loading(false))
    })
  }

  static post(url, data, headers) {
    return new Promise((resolve, reject) => {
      this.request('POST', url, data, headers)
        .then((response) => resolve(response))
        .catch((error) => reject(error))
    })
  }
}

export default DownloadApi
