const setElementStyle = function (element, prop, value, isClass = true) {
  let div = isClass ? document.getElementsByClassName(element) : document.getElementById(element)
  if (div && div.setAttribute)
    div.setAttribute("style", `${prop}: ${value} !important`);
  else
    document.head.appendChild(document.createElement("style")).innerHTML = `${element} {${prop}: ${value} !important}`;
}

const setPseudoElementStyle = function (parent, pseudo, prop, value, isClass = true) {
  let div = isClass ? document.getElementsByClassName(parent) : document.getElementById(parent)
  if (div && div.pseudoStyle)
    div.pseudoStyle(pseudo, prop, value);
  else
    document.head.appendChild(document.createElement("style")).innerHTML = `${parent}${pseudo} {${prop}: ${value}}`;
}

const setBeforeStyle = function (parent, prop, value) {
  setPseudoElementStyle(parent, "::before", prop, `${value} !important`)
}

const setAfterStyle = function (parent, prop, value) {
  setPseudoElementStyle(parent, "::after", prop, `${value} !important`)
}

const options = {
  size     : 50,
  thickness: 2,
  color    : '#FF5722',
}

export {
  setElementStyle,
  setBeforeStyle,
  setAfterStyle,
  setPseudoElementStyle,

  options
}
