<template>
  <div id="preload" class="position-fixed justify-content-center align-items-center"
       :class="`${show.image ? '':'bg-color-1'} ${className}`">
    <img src="@/assets/img/logo.png" class="animated zoomIn" v-if="show.image"/>
    <div class="square-container" :class="{'rotate-square': currentAnimate > 4}" v-else>
      <div class="square-line square-top bg-white"
           :class="{'in': animate === 'square-line-1', 'show': currentAnimate >= 1}"></div>
      <div class="square-line square-left bg-white"
           :class="{'in': animate === 'square-line-4', 'show': currentAnimate >= 4}"></div>
      <div class="square-line square-right bg-white"
           :class="{'in': animate === 'square-line-2', 'show': currentAnimate >= 2}"></div>
      <div class="square-line square-bottom bg-white"
           :class="{'in': animate === 'square-line-3', 'show': currentAnimate >= 3}"></div>
    </div>
  </div>
</template>

<script>

  let interval

  import TripleSpinner from "./Loader/TripleSpinner";

  export default {
    name: "PreLoad",

    components: {
      TripleSpinner,
    },

    data() {
      return {
        className     : "d-flex",
        animate       : false,
        currentAnimate: 0,
        show          : { image: false }
      }
    },

    created() {
      this.run()
    },

    methods: {

      close() {
        this.className = "d-flex animated fadeOut"
        setTimeout(() => {
          this.className = "d-none"
          clearInterval(interval)
          this.$store.dispatch('preloading', false)
        }, 1000)
      },

      showImage() {
        setTimeout(() => {
          this.show.image = true
          this.close()
        }, 1700)
      },

      run() {
        let self = this, i = 0, length = 4
        this.animate = true
        interval = setInterval(() => {
          this.currentAnimate = i++
          if (i > length && (document.readyState === "complete" || document.readyState === "ready"))
            self.showImage();// self.close()
          else this.animate = `square-line-${i}`;
        }, 500)

      }
    }
  }
</script>

<style scoped lang="scss">

  #preload {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 16777271;
    background-color: #fff;
    transition: background-color .5s;
  }

  .square-container {
    position: relative;
    height: 82px;
    width: 82px;
    transition: all .9s;

    &.rotate-square {
      transform: rotate(9225deg);
    }

    .square-line {
      position: absolute;
      margin-left: auto;
      margin-right: auto;
      /*transition: all 1s;*/

      &.square-top, &.square-bottom {
        height: 4px;
        width: 0;

        &.in, &.show {
          width: 82px;
        }
      }

      &.square-left, &.square-right {
        width: 4px;
        height: 0;

        &.in, &.show {
          height: 82px;
        }
      }

      &.square-top {
        top: 0;

        &.in {
          -webkit-animation: animate-square-top .5s linear;
          -moz-animation: animate-square-top .5s linear;
          -o-animation: animate-square-top .5s linear;
          animation: animate-square-top .5s linear;
        }
      }

      &.square-left {
        top: 0;
        left: 0;
        transform: scaleX(-1);

        &.in {
          -webkit-animation: animate-square-left .5s linear;
          -moz-animation: animate-square-left .5s linear;
          -o-animation: animate-square-left .5s linear;
          animation: animate-square-left .5s linear;
        }
      }

      &.square-right {
        top: 0;
        right: 0;

        &.in {
          -webkit-animation: animate-square-left .5s linear;
          -moz-animation: animate-square-left .5s linear;
          -o-animation: animate-square-left .5s linear;
          animation: animate-square-left .5s linear;
        }
      }

      &.square-bottom {
        bottom: 0;
        left: 0;

        &.in {
          -webkit-animation: animate-square-top .5s linear;
          -moz-animation: animate-square-top .5s linear;
          -o-animation: animate-square-top .5s linear;
          animation: animate-square-top .5s linear;
        }
      }
    }
  }

  @keyframes animate-square-top {
    from {
      width: 0;
      /*-webkit-transform: translateY(-1000px);
      -moz-transform: translateY(-1000px);
      -o-transform: translateY(-1000px);
      transform: translateY(-1000px);*/
    }
    to {
      width: 82px;
      /*-webkit-transform: translateY(0);
      -moz-transform: translateY(0);
      -o-transform: translateY(0);
      transform: translateY(0);*/
    }
  }

  @keyframes animate-square-left {
    from {
      height: 0;
    }
    to {
      height: 82px;
    }
  }

  @keyframes animate-square-right {
    from {
      -webkit-transform: translateX(1000px);
      -moz-transform: translateX(1000px);
      -o-transform: translateX(1000px);
      transform: translateX(1000px);
    }
    to {
      -webkit-transform: translateX(0);
      -moz-transform: translateX(0);
      -o-transform: translateX(0);
      transform: translateX(0);
    }
  }

  @keyframes animate-square-bottom {
    from {
      -webkit-transform: translateY(1000px);
      -moz-transform: translateY(1000px);
      -o-transform: translateY(1000px);
      transform: translateY(1000px);
    }
    to {
      -webkit-transform: translateY(0);
      -moz-transform: translateY(0);
      -o-transform: translateY(0);
      transform: translateY(0);
    }
  }
</style>
