<template>
  <div class="circle-2" :style="{ height: shape.size + 'px', width: shape.size + 'px' }">

    <div class="spinner" :style="{ borderTopColor: shape.color, borderWidth: shape.thickness + 'px' }"></div>

    <div class="spinner-bg" :style="{ borderColor: color2, borderWidth: shape.thickness + 'px' }"></div>

  </div>
</template>

<script>

  import {options} from "../../../common/utils";

  export default {
    name: "TripleSpinner",

    props: {
      size     : Number,
      thickness: Number,
      color    : String,
      color2   : String,
    },

    watch: {
      size(value) {
        this.shape.size = value
      },

      thickness(value) {
        this.shape.thickness = value
      },

      color(value) {
        this.shape.color = value
      },

    },

    mounted() {
      this.run()
    },

    data() {
      return {
        shape    : {
          size     : options.size,
          thickness: options.thickness,
          color    : options.color,
        },
        className: '.circle-2'
      }
    },

    methods: {

      checkSizeProp() {
        this.shape.size = this.size || this.shape.size
      },

      checkColorProp() {
        this.shape.color = this.color || this.shape.color
      },

      checkThicknessProp() {
        this.shape.thickness = this.thickness || this.shape.thickness
      },

      checkAllProps() {
        this.checkSizeProp()
        this.checkColorProp()
        this.checkThicknessProp()
      },

      run() {
        this.checkAllProps()
      }
    }


  }
</script>

<style scoped>

  * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .circle-2 {
    position: relative;
  }

  .circle-2 .spinner,
  .circle-2 .spinner-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

  .circle-2 .spinner {
    z-index: 1;
    background-color: transparent;
    border: 2px solid transparent;
    border-top: 2px solid #03A9F4;
    -webkit-animation: 1s spin linear infinite;
    animation: 1s spin linear infinite;
  }

  .circle-2 .spinner-bg {
    border: 2px solid rgba(0, 0, 0, .5);
  }

  @-webkit-keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }


</style>
