<template>
  <div class="circle-1"
       :style="{
          borderTopColor: shape.color,
          height: shape.size + 'px',
          width: shape.size + 'px',
          borderWidth: shape.thickness + 'px'
       }">
  </div>
</template>

<script>

  import {setAfterStyle, setBeforeStyle, options} from "../../../common/utils";

  export default {
    name: "TripleSpinner",

    props: {
      size     : Number,
      thickness: Number,
      color    : String,
    },

    watch: {
      size(value) {
        this.shape.size = value
      },

      thickness(value) {
        this.shape.thickness = value
      },

      color(value) {
        this.shape.color = value
      },
    },

    mounted() {
      this.run()
    },

    data() {
      return {
        shape    : {
          size     : options.size,
          thickness: options.thickness,
          color    : options.color,
        },
        className: '.circle-1'
      }
    },

    methods: {

      checkSizeProp() {
        this.shape.size = this.size || this.shape.size
      },

      checkColorProp() {
        this.shape.color = this.color || this.shape.color
      },

      checkThicknessProp() {
        this.shape.thickness = this.thickness || this.shape.thickness
      },

      checkAllProps() {
        this.checkSizeProp()
        this.checkColorProp()
        this.checkThicknessProp()
      },

      run() {
        this.checkAllProps()
      }
    }


  }
</script>

<style scoped>

  .circle-1 {
    width: 75px;
    height: 75px;
    margin: 0;
    background: transparent;
    border-top: 4px solid #03A9F4;
    border-right: 4px solid transparent;
    border-radius: 50%;
    -webkit-animation: 1s spin linear infinite;
    animation: 1s spin linear infinite;
  }

  @-webkit-keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }


</style>
